@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
header{display:flex;justify-content:flex-start;grid-gap:2rem;gap:2rem;align-items:center;background-color:#00334b;border-bottom:4px solid #0cf}header .aot-logo{width:350px}header p{font-size:3rem;color:aliceblue}header span{color:#fff;font-size:4rem;margin-left:250px}main{padding-bottom:168px;width:100%;height:100%;display:grid;grid-gap:25px;gap:25px;grid-template-columns:repeat(auto-fill, minmax(400px, 1fr));justify-content:center;align-content:center;background-color:#c0e6e6}.card{margin-top:60px;width:100%;height:100%;font-size:2rem;justify-content:center;align-items:center;display:flex;flex-direction:column}.card img{border:4px solid #000}*,*::after,*::before{padding:0;margin:0;font-family:'Staatliches', cursive}

